<template>
  <div>
    <!-- page conten -->
      <div class="row justify-content-md-center">
        <div class="col-md-3.5 align-self-center">
          <div class="card card-signin my-5">
            <div class="card-body">
              <h5 class="card-title text-center">Sign In</h5>
              <form class="form-signin" @submit.prevent="handleSignIn">
                <div class="form-label-group">
                  <input v-model="user.username" type="text" id="inputEmail" class="form-control" placeholder="Email address" required autofocus>
                </div>
                <div class="form-label-group">
                  <input v-model="user.password" type="password" id="inputPassword" class="form-control" placeholder="Password" required>
                </div>
                <div class="custom-control custom-checkbox mb-3">
                  <input type="checkbox" class="custom-control-input" id="customCheck1">
                  <label class="custom-control-label" for="customCheck1">Remember password</label>
                  <router-link to="/register"><button type="button" id="register" class="btn btn-success">Register</button></router-link>
                </div>
                <button class="btn btn-lg btn-primary btn-block text-uppercase">Sign in</button>
                <hr class="my-4">
                <button class="btn btn-lg btn-google btn-block text-uppercase"><i class="fab fa-google mr-2"></i> Sign in with Google</button>
                <button class="btn btn-lg btn-facebook btn-block text-uppercase"><i class="fab fa-facebook-f mr-2"></i> Sign in with Facebook</button>

                <div class="error-message" v-if="message">{{message}}</div>
              </form>
            </div>
          </div>
        </div>
      </div>
      <!-- page conten -->
  </div>
</template>

<script>
import User from '@/model/UserFormLogin'

export default {
  name: 'LoginPage',
  components: {
    User
  },
  data () {
    return {
      user: new User('', '', ''),
      message: ''
    }
  },
  computed: {
    // lay trang thai login hay khong , de vao trang profile => ta lay tu state status
    loggIn () {
      return this.$store.state.authStoreModule.status.loggin
    }
  },

  // Luc dau neu trong tinh trang loggin roi thi tu dong vao trang profile
  created () {
    if (this.loggin) {
      // navigate to a different URL
      this.$router.push('/user-board')
    }
  },

  methods: {
    handleSignIn () {
      //
      if (this.user.username && this.user.password) {
        this.$store.dispatch('authStoreModule/loggIn', this.user).then(
          // login success
          (response) => {
            alert(response.data)
            this.$router.push('/user-board')
          },
          // login failed
          error => {
            this.message = (error.response && error.response.data && error.response.data.message) ||
                error.message ||
                error.toString()
            console.log(error)
          }
        )
      }
    }
  }
}
</script>

<style scoped>

.card-signin {
  border: 0;
  border-radius: 1rem;
  box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.1);
}

.card-signin .card-title {
  margin-bottom: 2rem;
  font-weight: 300;
  font-size: 1.5rem;
}

.card-signin .card-body {
  padding: 2rem;
}

.form-signin {
  width: 100%;
}

.form-signin .btn {
  font-size: 80%;
  border-radius: 5rem;
  letter-spacing: .1rem;
  font-weight: bold;
  padding: 1rem;
  transition: all 0.2s;
}

#register {
  font-size: 14px;
  padding: 0.1rem 0.9rem;
  border-radius: 5px;
  margin-left: 110px;
  font-weight: 300;
}

.form-label-group {
  position: relative;
  margin-bottom: 1rem;
}

.form-label-group input {
  border-radius: 8px;
}

.form-label-group>label {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  margin-bottom: 0;
  /* Override default `<label>` margin */
  line-height: 1.5;
  color: #495057;
  border: 1px solid transparent;
  border-radius: .25rem;
  transition: all .1s ease-in-out;
}

input {
  padding-left: auto 10px !important;
}

.custom-control {
  display: flex;
}

.btn-google {
  color: white;
  background-color: #ea4335;
}

.btn-facebook {
  color: white;
  background-color: #3b5998;
}

/* Error style
---------------------------------- */
.error-message {
  color: #ea4335;
  background-color: bisque;
  border-radius: 8px;
  padding: 10px 10px;
  margin: 8px 5px 0px 5px;
  text-shadow: 1px 0 7px red;
  font-size: 1.1rem;
}

/* Fallback for Edge
-------------------------------------------------- */

@supports (-ms-ime-align: auto) {
  .form-label-group>label {
    display: none;
  }
  .form-label-group input::-ms-input-placeholder {
    color: #777;
  }
}

/* Fallback for IE
-------------------------------------------------- */

@media all and (-ms-high-contrast: none),
(-ms-high-contrast: active) {
  .form-label-group>label {
    display: none;
  }
  .form-label-group input:-ms-input-placeholder {
    color: #777;
  }
}
</style>



